<template>
	
	<view class="container">
		<view class="moment-item" v-for="(item,index) in momentList" :key="index">
			<view class="tn-flex tn-flex-col-center">
				<tn-avatar class="user-avatar" size="lg"
					src="https://resource.tuniaokj.com/images/avatar/xiaomai4.jpg"></tn-avatar>
				<span class="margin-right-5">{{item.nickname}}</span>
				<span class="margin-right-5">{{item.createTime}}</span>
			</view>
			<view class="content" v-highlight  v-html="item.content">
				
			</view>
		</view>
	</view>
</template>

<script>
	import {
		getForumList
	} from '@/api/moment.js'
	export default {
		data() {
			return {
				content: "&lt;script&gt;alert(1)&lt;/script&gt;",
				momentList:[],
				params:{
					pageNo:1,
					pageSize:10
				},
				pages:0
			}
		},
		onLoad() {
			this.getForumList()
		},
		methods: {
			getForumList(){
				getForumList(this.params).then(res =>{
					this.momentList = res.data.records
					this.pages = res.data.pages
				})
			},
		}
	}
</script>

<style lang="scss">
	.container{
		padding: 10px;
		.moment-item{
			background-color: #fff;
			margin: 10px 0;
			padding: 10px;
			.content{
				margin-top: 15px;
				overflow-x: auto !important;
			}
		}
		.margin-right-5{
			margin-right: 5px;
		}
	}
</style>